.footer
  background: #fff
  padding: 0
  margin-top: 60px
  letter-spacing: 0
  
  .all
    display: flex
    gap: 60px
    padding-bottom: 40px
    padding-top: 58px
    position: relative
    &:before
      content: ""
      height: 2px
      width: 100%
      position: absolute
      top: 0
      left: 0
      background: radial-gradient(96.63% 96.72% at 100% 100%, #F26135 0%, #FF9950 100%)
  
  &-left
    flex: 1 1 auto
    min-width: 0
  
  &-right
    flex: 0 0 320px
    display: flex
    flex-direction: column
    gap: 80px
  
  &-nav
    list-style: none
    margin: 0 0 70px
    padding: 0
    display: flex
    flex-wrap: wrap
    gap: 52px
    font-size: 15px
    li
      display: inline-block
      
      a
        color: $black700
        text-decoration: none
        transition: color $transitionSpeed $ease
        &:hover
          color: $black1000
  
  &-submenu
    display: flex
    gap: 28px
    
    .col
      flex: 1 1 calc(33% - 28px)
      max-width: calc(33% - 28px)
      display: flex
      flex-direction: column
      gap: 32px
      
    .submenu-title
      padding: 17px 0
      font-size: 20px
      font-weight: 600
      &:first-child
        margin-top: 0
      
      &__link
        color: $black700
        text-decoration: none
        display: flex
        align-items: center
        gap: 14px
        svg
          opacity: 1
          stroke: #D9D9D9
          transition: all 0.3s ease
          transform: translateX(0px)
        &:hover
          color: $black1000
          svg
            stroke: #F26135
            transform: translateX(3px)
    .submenu-list
      list-style: none
      margin: 0
      padding: 0
      display: flex
      flex-direction: column
      gap: 14px
      
      .submenu-item
        display: flex
        
        &__col
          display: inline-block
          width: 50%
          vertical-align: top
          padding-right: 8px
        
        a
          font-size: 15px
          color: $black700
          text-decoration: none
          display: block
          &:hover
            color: $black1000
  
  &-support
    &__btn
      max-width: 280px
    
    &__title
      font-size: 16px
      color: $black400
      margin-bottom: 12px
    
    &__phone
      font-size: 20px
      font-weight: 600
      color: $black700
      margin-bottom: 16px
      position: relative
      svg
        position: absolute
        top: 4px
        left: -32px
      a
        color: $black700
        text-decoration: none
        transition: color $transitionSpeed $ease
        &:hover
          color: $orangeMain500
    
    &__btn
      .btn
        width: 100%
  
  &-local
    font-size: 16px
    color: $black700
    line-height: 1.6
    margin-bottom: 24px
    
    strong
      font-weight: 600
      color: $black700
      font-size: 20px
      display: block
      position: relative
      svg
        position: absolute
        top: 9px
        left: -32px
  
  &-email
    margin-bottom: 32px
    
    &__title
      font-size: 16px
      color: $black700
      margin-bottom: 8px
    
    &__link
      a
        font-size: 20px
        color: $black700
        text-decoration: none
        transition: color $transitionSpeed $ease
        display: block
        position: relative
        svg
          position: absolute
          top: 6px
          left: -32px
        &:hover
          color: $black1000
  
  &-socs
    list-style: none
    margin: 0 0 0 -32px
    padding: 0
    display: flex
    gap: 36px
    
    li
      display: inline-block
      
      a
        display: flex
        align-items: center
        justify-content: center
        width: 36px
        height: 36px
        transition: all $transitionSpeed $ease
        color: $black700
        
        .svg-icon
          width: 36px
          height: 36px
          fill: $orangeMain500
        &:hover
          .svg-icon
            fill: $orangeMain600
  
  &-bottom
    .all
      display: flex
      align-items: flex-end
      justify-content: space-between
      gap: 20px
      padding: 28px 0
      margin-top: 28px
      border-top: 1px solid #E1E4E7
      &:before
        display: none
    
    &__left
      display: flex
      align-items: flex-end
      gap: 24px
    
    &__logo
      display: flex
      align-items: center
      position: relative
      top: -3px
      
      img
        height: 32px
        width: auto
    
    &__copyright
      font-size: 14px
      color: $black700
    
    &__right
      display: flex
      gap: 36px
    
    &__link
      font-size: 14px
      color: $black700
      text-decoration: none
      transition: color $transitionSpeed $ease
      &:hover
        color: $black1000
  
  &-wrapper
    padding: 40px 0 30px
    background: $baseBg
    margin-top: 140px
    +r($md)
      margin-top: 80px
