.main-info-boxes
  display: flex
  gap: 12px
  flex-direction: column
  color: $black700
  .box-row
    display: flex
    gap: 12px
  .box-col1
    display: flex
    flex-direction: column
    gap: 12px
    flex: 0 0 100%
  .box-col2
    display: flex
    flex-direction: column
    gap: 12px
    flex: 0 0 calc(50% - 6px)
    
.main-info-box
  background: #fff
  border: 1px solid #E1E4E7
  border-radius: 10px
  padding: 24px 24px 24px 28px
  display: flex
  gap: 24px
  font-size: 16px
  flex: 1 1 auto
  box-shadow: 0px 1px 2px 0px #0000001A
  line-height: 1.2
  position: relative
  transition: all $transitionSpeed $ease
  .icon-bg
    position: absolute
    bottom: 0
    right: 28px
    color: #F0F2F4
    overflow: hidden
    svg
      width: 64px
      height: 64px
      stroke: currentColor
      transition: all 0.3s $ease
      &.stroke-none
        stroke: none

  &:hover
    box-shadow: 0px 1px 2px 0px #0000001A
    border-color: #CBD2D9
    background: #E1E4E7
    .icon-bg
      color: #CBD2D9
      svg
        margin-bottom: 5px
  .num
    color: $black700
    font-size: 56px
    font-weight: 300
    line-height: .9

  .title
    font-size: 18px
    color: $black700
    
  p
    margin: 0
    color: #8C8C8C
    padding: 20px 0 4px
  .b-link
    margin-top: 10px
  .link-plus
    color: $orangeMain500
    font-size: 16px
    display: flex
    align-items: center
    gap: 8px
    svg
      stroke: currentColor
.box-dates
  display: flex
  gap: 24px
  flex-direction: column
  width: 100%
  &__header
    display: flex
    gap: 24px
    width: 100%
  .dates
    &-wrap
      border-right: 1px solid $black100
      padding-right: 24px
      line-height: 1
      display: flex
      flex-direction: column
      gap: 6px
    .date-label
      font-size: 18px
      font-weight: 450
  .b-right
    margin-left: auto
    
.box-price
  display: flex
  gap: 24px
  flex-direction: column
  width: 100%
  &__header
    display: flex
    gap: 24px
    width: 100%
  &__toggler
    display: none
    &.--active
      display: block
  .price
    color: $black400
    font-size: 14px
    display: flex
    align-items: center
    &-wrap
      border-right: 1px solid $black100
      padding-right: 24px
      line-height: 1
      display: flex
      flex-direction: column
      gap: 6px
    &-label
      font-size: 20px
      font-weight: 600
      color: $black700
      white-space: nowrap
  .info-text
    color: $black700
    display: flex
    gap: 24px
    flex: 1 1 auto
    .b-text
      display: flex
      align-items: center
    .b-right
      margin-left: auto
